export default {
	data() {
		return {
			isCanUse:true,
			height:"",
			background:{
				backgroundImage:"linear-gradient(90deg, #00ABEB, #54C3F1)",
			},
		};
	},
	methods: {
		// 用户点击授权
		wxGetUserInfo() {
			uni.login({
				success(res) {
					let code = res.code
					//获取用户信息
					uni.getUserInfo({
						success(data) {
							// console.log(data);
							uni.request({
									url: "https://applet.51tiaoyin.com/public/applet/login/",
									method: "GET",
									dataType:JSON,
									data: {
										"code": code,
										"iv": data.iv,
										"encryptedData": data.encryptedData
									},
									success(res) {
										// JSON.stringify(res.data.wechat_name);
										console.log(res);
										console.log(JSON.parse(res.data));
										const data = JSON.parse(res.data);
										console.log(data.data.uid);
										if(data.code == 200){
											uni.showLoading({
											    title: '登录中'
											});
											setTimeout(
											()=>{
											uni.reLaunch({
												url:"../index/index"
											})	
											},1500);
											// 把用户信息写入缓存
											
											// console.log(res.data.data);
											uni.setStorageSync('user_name', data.data.wechat_name);
											uni.setStorageSync('user_avatar', data.data.wechat_img);
											uni.setStorageSync('user_uid', data.data.uid);
										}else{
											uni.showToast({
												title: "服务器无响应"
											});
										}
										
									},
									fail(res) {
										uni.showToast({
											title: '获取授权信息失败',
											icon: 'none'
										});
									}
							})
						},
						fail(res) {
							uni.showToast({
								title:"无网络..."
							})
						}
					})
				}
			})
		},
	},
	onLoad() {
		
	}
}
